import React, { Component } from 'react';
import './index.scss'
class Photos extends Component {
    constructor() {
        super()
        this.state = {
            jonsArr: [
                {
                    id: '001',
                    title: 'CSS3 3D魔方旋转动画特效',
                    url: 'https://huzheng12.github.io/doujiaxiang/index.html'
                }
            ]
        }
    }
    tiao = (a) => {
        window.open(a.url)
    }
    render() {

        return (
            <div className='box-warp-ton clear'>
                <h2 className='h2_box'>案例</h2>
                <div className="case_box">
                    {
                        this.state.jonsArr.map((item, index) => {
                            return <div className="case_size_box" key={item + index} onClick={() => this.tiao(item)}>
                                {item.title}
                            </div>
                        })
                    }
                </div>
            </div>
        );
    }
}

export default Photos;