import React, { Component } from 'react';
import './index.scss'
import { history } from '../../../utils/history'
class Journal extends Component {
    state = {
        flg: true,
        arr: [
            {
                title: "React路由传参的三种方式",
                _id: "/home/xiangq/a0001",
                id: '0001',
                laiyuan: "来源==简书"
            },
            {
                title: "img标签",
                _id: "/home/xiangq/a0002",
                id: '0002',
                laiyuan: "原创搜集"
            },
            {
                title: "阿里云 服务器 mongodb 新的安装方法  步骤 ",
                _id: "/home/xiangq/a0003",
                id: '0003',
                laiyuan: "原创搜集"
            },
        ],
        codeType: true
    }
    tiaozhuan = (_url) => {
        history.push(_url)
    }
    render() {
        const {
            arr,
        } = this.state
        return (
            <div className='box-warp-ton clear'>
                <h2 style={{ paddingLeft: 10, fontWeight: 900, fontSize: 24, width: '80%', margin: '0 auto' }}>日志</h2>
                <div className="box-left-jounal">
                    <div className="box-nav-jounal clear" >
                        <div className="liebiao-book g-scrollbar" ref="oneone" style={{ height: sessionStorage.token ? 500 : 580, display: "block", overflowY: "auto" }} >
                            {
                                arr.map((item, i) => {
                                    return (
                                        <li key={i} className="clear" style={{ cursor: 'pointer' }} onClick={() => this.tiaozhuan(item._id)}>
                                            <span className="id_box">
                                                {
                                                    item.id
                                                }
                                            </span>
                                            <span className="li_box_link" >{item.title}</span>
                                            <span className="laiyuanbox">
                                                {
                                                    item.laiyuan
                                                }
                                            </span>
                                        </li>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Journal;