import React, { Component } from 'react';
import './index.scss'
import { history } from '../../../utils/history'
class Xiangq extends Component {
    fanhuigo = () => {
        history.go(-1)
    }
    render() {
        const {
            arr
        } = this.props
        return (
            <div className='box-warp-ton'>
                <div style={{ width: "100%", height: 100 }}>
                    <div className="bg1" style={{ width: "80%", color: "#fff", lineHeight: "50px", fontSize: 32, textAlign: "center", borderRadius: "8px", margin: "0 auto" }}>
                        <h1>{arr.title}</h1>
                    </div>
                    <div className="dianzan-xianqi" style={{ width: "100%" }}>
                        {/* <button onClick={this.fanhuigo}>返回</button> */}
                        {/* |<button>点赞</button>|
                        <button>转发</button>|
                        <button>分享</button>|
                        <button>复制地址</button>| */}
                    </div>
                </div>
                <div style={{ padding: "20px 80px 80px 80px " }} dangerouslySetInnerHTML={{ __html: arr.consten }}>
                    {/* {arr.consten} */}
                </div>
                <div className="xianq-footer clear">
                    <div style={{
                        width: '100%',
                        marginLeft: '152px',
                        display: 'flex',
                        flexDirection: 'column'
                    }}>
                        <span style={{ marginRight: 15 }}>时间：{arr.time}</span>
                        <span style={{ marginRight: 15 }}>来源：{arr.name}</span>
                    </div>
                </div>
            </div >
        );
    }
}

export default Xiangq;