import React, { Component } from 'react';
import './index.scss'


class guestbook extends Component {
    render() {
        return (
            <div className='box-warp-ton' style={{ padding: "15px 100px" }}>
                <h2 style={{ fontSize: "26px", fontWeight: 900, fontFamily: "Georgia" }}>
                    留言板
                </h2>
                <h3 style={{ lineHeight: "30px", borderBottom: "1px solid rgb(195, 186, 186)", padding: "10px 0" }}>
                    <span style={{ marginRight: "10px" }}>主人寄语</span>
                </h3>
                <div style={{ display: "block", height: 35, fontSize: 24, color: "red", textAlign: "center", paddingTop: 10 }}>
                    <p>
                        用于记录和学习   不喜勿喷
                    </p>
                    您好！暂不支持发表留言，如需建议请前往 <a href="https://huzhengmm.cn">https://huzhengmm.cn</a>
                </div>
            </div>
        );
    }
}
export default guestbook;