import React, { Component } from 'react';
import './index.scss';
import axios from '../../../utils/ajax';
import Swipers from '../../components/swiper';
import { HashRouter as Hash, Route, Switch, Link } from "react-router-dom"
const StyleBox = {
    height: 33,
    lineHeight: "33px",
    fontSize: "13px",
    borderBottom: "1px solid #eee",
    display: 'flex'
}
class Shouiye extends Component {
    state = {
        arr: [
            {
                img: require('../../img/180715imAMIzUL.jpg'),
                title: "2020年1月1日结婚",
                centent: "点击进入官方邀请函",
            },
            {
                img: require('../../img/0_3.jpg'),
                title: ""
            },
            {
                img: require('../../img/art2.jpg'),
                title: ""
            },
            {
                img: require('../../img/1_3.jpg'),
                title: ""
            },
            {
                img: require('../../img/2_3.jpg'),
                title: ""
            },
        ],
        webarr: [],
        jonsArr: [
            {
                id: '001',
                title: 'CSS3 3D魔方旋转动画特效',
                url: 'https://huzheng12.github.io/doujiaxiang/index.html'
            }
        ]

    }
    tiao = (a) => {
        window.open(a.url)
    }
    render() {
        const {
            arr,
            webarr,
            jonsArr
        } = this.state
        return (
            <div className="conten-home clear">
                <div style={{
                    padding: 10, width: 1050,
                    display: "inline-block"
                }}>
                    <Swipers />
                    {/* 推荐文章  轮播图右侧 */}
                    <div style={{ float: "right" }}>
                        <h2 className="swiper-right-li">
                            <span className="iconfont icon-zan1 colorhu" style={{ fontSize: 16 }}></span>
                            <span>原创笔记记录</span>
                        </h2>
                        <div>
                            {/* 数据请求数据  8条数据 */}
                            <div style={StyleBox}>
                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                <Link className="xuhao-text-swper" to="/home/xiangq/a0001">React路由传参的三种方式</Link>
                            </div>
                            <div style={StyleBox}>
                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                <Link className="xuhao-text-swper" to="/home/xiangq/a0002">img标签</Link>
                            </div>
                            <div style={StyleBox}>
                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                <Link className="xuhao-text-swper" to="/">--</Link>
                            </div>
                            <div style={StyleBox}>
                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                <Link className="xuhao-text-swper" to="/">--</Link>
                            </div>
                            <div style={StyleBox}>
                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                <Link className="xuhao-text-swper" to="/">--</Link>
                            </div>
                            <div style={StyleBox}>
                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                <Link className="xuhao-text-swper" to="/">--</Link>
                            </div>
                            <div style={StyleBox}>
                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                <Link className="xuhao-text-swper" to="/">--</Link>
                            </div>
                            <div style={StyleBox}>
                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                <Link className="xuhao-text-swper" to="/">--</Link>
                            </div>
                        </div>
                    </div>
                    {/*  */}
                </div>
                <div className="conten-home-inde clear">
                    <h2 className="h2-content-home">个人生活介绍</h2>
                    <ul>
                        {
                            arr.map((item, i) => {
                                return (
                                    <li key={i} style={{ float: "left", width: 208, padding: "20px 0" }} >
                                        <a href="http://qingjian.myhhr.top/weddingdas/6239022.html" target="_blank" className="home-a-hezi">
                                            <img src={item.img} alt="" />
                                            <div className="ome-a-hezis">

                                            </div>
                                            <div className="home-a-hezis">
                                                <p style={{ textAlign: "center" }}>
                                                    {
                                                        item.title
                                                    }
                                                </p>
                                                <p style={{ textAlign: "center" }}>
                                                    {
                                                        item.centent
                                                    }
                                                </p>
                                            </div>

                                        </a>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
                <div className="conten-neirong-ho clear" style={{ paddingTop: "20px" }}>
                    <div className="neirong-left">
                        <h2>
                            <span className="icon-xuhao1 iconfont" style={{ paddingLeft: 18, color: "#996c39", fontSize: "16px", marginRight: "5px" }}>

                            </span>
                            <span style={{ fontWeight: 900 }}>最新笔记</span>
                        </h2>
                        {/* 循环渲染日志内容 */}
                        {
                            webarr.map((item, i) => {
                                return (
                                    <div className="conten-bomas" key={i}>
                                        <h3>{item.title}</h3>
                                        <p className="shouye-text-lib">发布时间：{item.time} 作者：{item.name} 分类：{item.cart}</p>
                                        <div className="content-web-gert">
                                            <dt style={{
                                                float: "left",
                                                marginRight: "15px"
                                            }} >
                                                <img src={item.img || "http://www.lmlblog.com/blog/11/images/4.jpg"} alt="" style={{
                                                    borderRadius: 3,
                                                    width: 200,
                                                    height: 138,
                                                }} />
                                            </dt>
                                            <dd style={{
                                                float: "left",
                                                width: 439
                                            }}>
                                                <div className="shouye-content" dangerouslySetInnerHTML={{ __html: item.consten }}>

                                                </div>
                                                <Link to={"/home/xiangq?id=" + item._id} onClick={() => {
                                                    document.documentElement.scrollTop = document.body.scrollTop = 339;
                                                }} className="but-web">查看详情</Link>
                                            </dd>
                                        </div>

                                    </div>
                                )
                            })
                        }

                    </div>
                    <div className="nerrong-right">
                        <div style={{ float: "right" }}>
                            <h2 className="swiper-right-li">
                                <span className="iconfont icon-zan1 colorhu" style={{ fontSize: 16 }}></span>
                                <span>推荐案例</span>
                            </h2>
                            <div>
                                {/* 数据请求数据  8条数据 */}

                                {
                                    jonsArr.map((item, index) => {
                                        return <div className="case_size_box" key={item + index} onClick={() => this.tiao(item)}>

                                            <div style={StyleBox}>
                                                <span className="iconfont icon-xuhao-dian colorhu" style={{ fontSize: 9 }}></span>
                                                <Link className="xuhao-text-swper" to="/home/xiangq/a0001">   {item.title}</Link>
                                            </div>
                                        </div>
                                    })
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Shouiye;