import React, { Component } from 'react';
import { Link } from "react-router-dom"
import './index.scss'
class HeadNav extends Component {
    state = {
        arr: [
            { title: "主页", link: "/", active: "nav-active", },
            { title: "日志", link: "/home/journal", active: "", },
            { title: "留言板", link: "/home/guestbook", active: "", },
            { title: "案例", link: "/home/case", active: "", },
            { title: "说说", link: "/home/resume", active: "", },
            { title: "手机验证码", link: "/home/webdata", active: "", },
            { title: "暂未开放", link: "/home/tempe", active: "", }
        ],

    }
    componentWillMount() {
        let liswee = window.location.hash.replace(/#\/home\//, "")
        var glf = true
        this.state.arr.map((item, i) => {
            let actives = item.link.replace(/\/home\//, "")
            item.active = ""
            if (liswee == actives) {
                item.active = "nav-active"
                return glf = false
            }
        })
        if (liswee.split("?")[0] == "xiangq") {
            this.state.arr[1].active = "nav-active"
            return glf = false
        }
        if (glf) {
            this.state.arr[0].active = "nav-active"
        }
    }
    componentWillUpdate() {
        let liswee = window.location.hash.replace(/#\/home\//, "")
        var glf = true
        this.state.arr.map((item, i) => {
            let actives = item.link.replace(/\/home\//, "")
            item.active = ""
            if (liswee == actives) {
                item.active = "nav-active"
                return glf = false
            }
        })
        if (liswee.split("?")[0] == "xiangq") {
            this.state.arr[1].active = "nav-active"
            return glf = false
        }
        if (glf) {
            this.state.arr[0].active = "nav-active"
        }
    }
    render() {
        const {
            arr
        } = this.state
        return (
            <div className="nav-wap">
                <ul className="clear" style={{
                    width: 1050,
                    margin: "0 auto",
                    fontSize: 16
                }}>
                    {
                        arr.map((item, i) => {
                            return (
                                <li key={i}>
                                    <Link to={item.link} className="Nav-link-text" id={item.active} >{item.title}
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </div >
        );
    }
}

export default HeadNav;