import React, { Component } from 'react'
import JournalData from '../../../utils/data'
import Xiangq from './idnex'
export default class Xq0001 extends Component {
  constructor() {
    super()
    this.state = {
      arr: {}
    }

  }
  componentDidMount() {
    this.setState({
      arr: JournalData[this.props.match.params.id]
    })
  }
  render() {
    const consten = this.state.arr && this.state.arr._html


    return (
      <div>
        <Xiangq arr={{
          title: this.state.arr && this.state.arr.title,
          time: this.state.arr && this.state.arr.time,
          name: this.state.arr && this.state.arr.name,
          consten,
          id: this.state.arr && this.state.arr.id
        }}></Xiangq>
      </div>
    )
  }
}
